.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

h2 {
  text-align: center;
}

li {
  margin-bottom: 16px;
  border-bottom: 1px solid black;
  display: flex;
  justify-content: space-between;
}

li > span {
  width: 180px;
  display: inline-block;
}

